
import { defineComponent } from 'vue'
import {
  IonRadioGroup,
  IonRadio,
  IonItem,
  IonLabel,
  IonList,
  RadioGroupCustomEvent,
} from '@ionic/vue'

export default defineComponent({
  name: 'RadioInputList',
  components: {
    IonRadioGroup,
    IonRadio,
    IonItem,
    IonLabel,
    IonList,
  },
  props: {
    list: {
      type: Array,
      default: () => [],
    },
    modelValue: {
      type: Number,
      default: null,
    },
  },
  setup(props, { emit }) {
    function setOption(event: RadioGroupCustomEvent<number>) {
      emit('update:model-value', event.detail.value)
    }

    return {
      setOption,
    }
  },
})
